import React from 'react';
import { Grid } from 'material-ui';

export const LayoutEngine = (getWdigetById, layouts) => {
  return layouts.map((layout, i) => {
    if (layout.flex === 'row') {
      return (
        <Grid key={i + 'layout'} container style={{ paddingBottom: 20 }} {...layout.frameworkWebProps}>
          {layout.items.map((item, j) => (
            <Grid key={i + 'layout' + j} item style={{ display: 'grid' }} md={item.frameworkWebProps?.size12 || true} xs={item.frameworkMobileProps?.size12 || true}>
              {getWdigetById(item.mfId, item.reactProps)}
            </Grid>
          ))}
        </Grid>
      );
    } else if (layout.flex === 'column') {
      return (
        <Grid key={i + 'layout'} container direction="column" style={{ paddingBottom: 20 }} {...layout.frameworkWebProps}>
          {layout.items.map((item, j) => (
            <Grid key={i + 'layout' + j} item style={{ display: 'grid' }} md={item.frameworkWebProps?.size12 || true} xs={item.frameworkMobileProps?.size12 || true}>
              {getWdigetById(item.mfId, item.reactProps)}
            </Grid>
          ))}
        </Grid>
      );
    }
  });
};

export const hasAccess = (perms, str) => {
  if (!perms) return true; // mobile
  const perm = perms[str];
  return !perm.includes('dummy');
};
