function emit(eventName, detail) {
  var event;
  if (window.CustomEvent) {
    console.log('creating window ' + eventName);
    event = new CustomEvent(eventName, { detail });
  } else {
    event = document.createEvent('CustomEvent');
    event.initCustomEvent(eventName, true, true, { detail });
  }
  window.dispatchEvent(event);
}

export const registerWithMobileRuntime = () => {
  window.onlaunch = (detail) => {
    console.log('onlaunch came into');
    emit('onLaunchEvent', detail);
  };

  window.onShow = (detail) => {
    console.log('onShow came into');
    emit('onShowEvent', detail);
  };

  window.onHide = (detail) => {
    console.log('onHide came into');
    emit('onHideEvent', detail);
  };

  window.onDeeplink = (detail) => {
    console.log('onDeeplink came into');
    emit('onDeeplinkEvent', detail);
  };

  const reqData = {
    muiLetId: 123,
    metadata: {
      version: 1.0,
    },
  };
  const jsonData = JSON.stringify(reqData);
  console.log('registerLifeCycleCallback ..', window.muiJSBridge);
  if (typeof window.muiJSBridge?.registerLifeCycleCallback === 'function') {
    console.log('reaching to registerLifeCycleCallback.....');
    window.muiJSBridge.registerLifeCycleCallback(jsonData);
  }
};

export const isMobile = (shouldRegister) => {
  if (window.muiJSBridge || window.location.href.startsWith('file://')) {
    if (shouldRegister) registerWithMobileRuntime();
    return true;
  }
};

export const register = (setLaunchOptions) => () => {
  if (!isMobile(true)) return;
  window.addEventListener('onLaunchEvent', ({ detail }) => setLaunchOptions(detail));
  return () => {
    window.removeEventListener('onLaunchEvent', setLaunchOptions);
  };
};
