import EventEmitter from 'eventemitter3';

const eventEmitter = new EventEmitter();
const Emitter = {
  on: (event, fromMfId, fn) => eventEmitter.on(fromMfId + '_' + event, fn),
  once: (event, fromMfId, fn) => eventEmitter.once(fromMfId + '_' + event, fn),
  off: (event, fromMfId, fn) => eventEmitter.off(fromMfId + '_' + event, fn),
  emit: (event, owner, payload, permittedListeners) => eventEmitter.emit(owner + '_' + event, payload),
};
Object.freeze(Emitter);

export default Emitter;
