import React from 'react';
import { navigateToUrl } from 'single-spa';
import { CSSTransition } from 'react-transition-group';
import { Typography } from 'material-ui';
import './assets/side.css';
import image from './assets/logo-nav.svg';

export default function Root(props) {
  const navs = props.navs;
  // console.log(navs, 'navs')

  const [isOpen, setIsOpen] = React.useState(true);
  const [selected, setSelected] = React.useState(window.location.pathname);

  const goto = (url) => {
    navigateToUrl(url);
    setSelected(url);
  };

  const open = (o) => {
    setIsOpen(o);
    if (document.getElementById('mui-nav-main').style['width'] == 'calc(100% - 280px)')
      document.getElementById('mui-nav-main').style.width = 'calc(100% - 80px)';
    else document.getElementById('mui-nav-main').style['width'] = 'calc(100% - 280px)';
  };

  return (
    <div className={!isOpen ? 'sidebar sidebar-closed' : 'sidebar'}>
      <button style={{ display: 'none' }} className={'sidebar__button'} onClick={() => open(!isOpen)}>
        =
      </button>
      <div style={{ textAlign: 'center', paddingTop: 20, paddingBottom: 20, marginLeft: -5 }}>
        <img src={image} alt="image" />
      </div>
      <ul>
        {navs.map((item) => (
          <li key={item.title}>
            <div
              className={'sidebar__listItem' + (selected === item.path ? ' selected' : '')}
              onClick={() => goto(item.path)}
            >
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" height="15px" width="15px">
                <path d="M0 96C0 60.65 28.65 32 64 32H448C483.3 32 512 60.65 512 96V416C512 451.3 483.3 480 448 480H64C28.65 480 0 451.3 0 416V96zM64 416H224V160H64V416zM448 160H288V416H448V160z"/>
              </svg>
              <CSSTransition in={isOpen} timeout={0} classNames={'fade'} unmountOnExit>
                <Typography>&nbsp;&nbsp;{item.title}</Typography>
              </CSSTransition>
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
}
